import React from 'react'
import './Feed.css';
import InputOption from './InputOption'
import AddIcon from '@material-ui/icons/Add';
import PlayCircleFilledIcon from '@material-ui/icons/PlayCircleFilled';
import ImageIcon from '@material-ui/icons/Image';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import MessageIcon from '@material-ui/icons/Message';
import SendIcon from '@material-ui/icons/Send';
import ShareIcon from '@material-ui/icons/Share';

function Feed() {
    return (
        <div className="feed">
             <div className="feed__top">
                 <p className="dashboard">Dashboard</p>
                 <p className="global">Global</p>
                 <p className="campus">Campus</p>
                 <p className="trends">Trends</p>
                 <p className="fileter">Filter</p>
             </div>
             <div className="feed__between">
                 <div className="profile__block">
                      <div className="centered__item">
                             <img className="profilephoto" src="data:image/jpeg;base64," alt=""/>
                             <p className="profile__name">Theresa Webb</p>
                             <p className="profile__portfolio">UI/UX Designer</p>
                      </div>
                      <div className="row__items">
                             <p className="followerclass">Followers</p>
                             <p className="followerno"> 22K</p>
                             <p className="profileviewsclass">Profile Views</p>
                             <p className="profileviewno">800</p>
                             <p className="savedclass">Saved</p>
                             <p className="savedno">5</p>
                      </div>
                 </div>
                 <div className="feed__inputContainer">
                      <div className="feed__input">
                        <form>
                            <input placeholder="Write Something here" type="text"/>
                            <button  type="submit">Send</button>
                        </form>
                      </div>
                      <div className="feed__inputOptions">
                      <InputOption Icon={ImageIcon} title='Photo' color="#B2BABB"/>
                      <InputOption Icon={PlayCircleFilledIcon} title='Video' color="#B2BABB"/>
                      <InputOption Icon={AddIcon} title='Add' color=" #B2BABB"/>
                      </div>
                 </div>
             </div>
             <div className="feed__down">
                 <div className = "feed__down__header">
                     <p><b>Robert Fox</b> likes this</p>
                 </div>
                 <div className="feed__down__between">
                 <img className="post__pic" src="data:image/jpeg;base64," alt="" />
                  <div className="post__info">
                      <h2 className="post__info__name">Jacob Jonnes</h2>
                      <p className="post__info__portfolio">Jr. UI/UX Developer</p>
                      <p className="post__info__time">3m ago</p>
                  </div>
                 </div>
                 <div className="message__body">
                     <p className="message__body__info">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor ....<p className="seemore">see more</p></p>
                     <img className="message__body__pic" src="data:image/jpeg;base64," alt="" />
                 </div>
                 <div className="bottom__up">
                    <InputOption Icon={ThumbUpAltIcon} title='Like' color="#B2BABB"/>
                    <InputOption Icon={MessageIcon} title='Comment' color="#B2BABB"/>
                    <InputOption Icon={ShareIcon} title='Share' color="#B2BABB"/>
                 </div>
                 <div className="bottom__down">
                     <img className="post__pic" src="data:image/jpeg;base64," alt=""/>

                     <div className="form__elements">
                        <form>
                            <input placeholder="Write Something here" type="text"/>
                            <SendIcon />
                        </form>
                     </div>
                 </div>
             </div>
        </div>
    )
}

export default Feed
